import React from 'react'

import {
  Image,
  Row,
  Col,
} from 'antd'

const ImageColumn = ({ item }) => (
  <Col sm={11} lg={11}>
    <Image
      width={400}
      src={item.imageSource}
      fallback="data:image/png;base64,"
    />
  </Col>
)

const ContentColumn = ({ item }) => (
  <Col sm={11} lg={12}>
    <h2 style={{ textAlign: 'left' }}>
      {item.contentTitle}
    </h2>
    <hr
      align="left" className="thick-line"
      style={{ marginBottom: '50px' }}
    />
    <p style={{ textAlign: 'left' }}>
      {item.contentText}
    </p>
  </Col>
)

const mainStyle = {
  display: 'flex',
  flexDirection: 'column',
}

const AlternatingImageText = ({ pageContent }) => {
  return (
    <div className="main-container" style={mainStyle}>
      {pageContent.map((item, i) =>
        <Row key={i} gutter={[60, 60]}
          style={{
            display: 'flex',
            alignContent: 'center',
            padding: '20px',
            margin: 0,
          }}
        >
          {
            i % 2 === 0
              ?
              (<div style={{ display: 'flex' }}>
                <ImageColumn item={item} />
                <ContentColumn item={item} />
              </div>)
              :
              (<div style={{ display: 'flex' }}>
                <ContentColumn item={item} />
                <ImageColumn item={item} />
              </div>)
          }
        </Row>
      )}
    </div>
  )
}

export default AlternatingImageText